import { defineComponent } from 'vue';
import DemoHomeNav from './DemoHomeNav.vue';
import { componentsList } from '../router/components';
export default defineComponent({
    name: 'demo-home',
    components: {
        DemoHomeNav
    },
    data() {
        return {
            componentsList
        };
    },
    methods: {
        jumpTo(name) {
            this.$router.push(name);
            // 向父窗口传递name
            window.parent.postMessage(name, '*');
        }
    }
});
